import React, { useState } from 'react'
import PageCover from '../page-cover'
import { signup } from '../../../core/services/signup-service'
import { Link } from 'react-router-dom'

export default function ContactPage () {
  const [firstName, setFirstName] = useState('')
  const [lastName, setLastName] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [passwordConfirm, setPasswordConfirm] = useState('')
  const [loading, setLoading] = useState(false)
  const [errorMessage, setErrorMessage] = useState(null)
  const [finished, setFinished] = useState(false)

  async function handleSubmit (ev) {
    ev.preventDefault()
    setLoading(true)
    setErrorMessage(null)

    if (password !== passwordConfirm) {
      setErrorMessage('Password confirm is not equal to password')
      return
    }

    try {
      await signup({
        firstName,
        lastName,
        email,
        password
      })

      setFinished(true)
    } catch (e) {
      setErrorMessage(e.message)
    } finally {
      setLoading(false)
    }
  }

  let content

  if (finished) {
    content = (
      <div className="alert alert-success">
        Registration completed with success, you can now login <Link to="/login">by clicking here</Link>
      </div>
    )
  } else {
    content = (
      <form onSubmit={handleSubmit}>
        <div className="row">
          <div className="col-md-6">
            <div className="row">
              <div className="col-md-6">
                <div className="form-group">
                  <label htmlFor="fname">First Name</label>
                  <input id="fname" className="form-control" type="text" value={firstName} onChange={e => setFirstName(e.target.value)} />
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group">
                  <label htmlFor="lname">Last Name</label>
                  <input id="lname" className="form-control" type="text" value={lastName} onChange={e => setLastName(e.target.value)} />
                </div>
              </div>
            </div>
            <div className="form-group">
              <label htmlFor="email">Email</label>
              <input id="email" className="form-control" type="email" value={email} onChange={e => setEmail(e.target.value)} />
            </div>
            <div className="form-group">
              <label htmlFor="password">Password</label>
              <input id="password" className="form-control" type="password" value={password} onChange={e => setPassword(e.target.value)} />
            </div>
            <div className="form-group">
              <label htmlFor="password-confirm">Confirm Password</label>
              <input id="password-confirm" className="form-control" type="password" value={passwordConfirm} onChange={e => setPasswordConfirm(e.target.value)} />
            </div>
            {errorMessage &&
              <div className="alert alert-danger">
                {errorMessage}
              </div>
            }
            <div className="form-group">
              <input className="btn btn-primary" type="submit" value={loading ? 'Wait' : 'Signup'} disabled={loading} />
            </div>
          </div>
        </div>
      </form>
    )
  }

  return (
    <React.Fragment>
      <PageCover title="Signup" />

      <section className="probootstrap-section">
        <div className="container">
          <h2 className="mb-5">Signup</h2>
          {content}
        </div>
      </section>
    </React.Fragment>
  )
}
